"use client";

// MODIFICATION: Added useEffect
import { useState, useEffect } from "react";
import {
  Play,
  CheckCircle2,
  Lock,
  Clock,
  Users,
  BookOpen,
  Trophy,
  Star,
  Sparkles,
  SquareDashed,
} from "lucide-react";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import Image from "next/image";
import { useUserStore } from "@/store/useUserStore";
import LearningSummary from "@/components/LearningSummary";
import MedicalTerminologyCard from "@/components/MedicalTerminologyCard";

type LessonStatus = "completed" | "in-progress" | "upcoming" | "locked";

export interface Lesson {
  id: string;
  title: string;
  description: string;
  status: LessonStatus;
  duration?: string;
  participants?: number;
  color: string;
  icon: React.ReactNode;
  link: string;
}

// ADDITION: This interface matches the data from your FastAPI backend
interface SyllabusInfo {
  id: number; // Assuming ID is a number from the database
  title: string;
  description: string;
  lesson_count: number;
}

// MODIFICATION: The hardcoded 'lessons' array is removed.
// We will fetch this data from the API.

// These are the static icons and colors your hardcoded array used.
// We can use them to style the data we get from the backend.
const ICONS = [
  <Star className="w-5 h-5 text-white" />,
  <CheckCircle2 className="w-5 h-5 text-white" />,
  <Play className="w-5 h-5 text-white" />,
  <Users className="w-5 h-5 text-white" />,
  <BookOpen className="w-5 h-5 text-white" />,
  <Lock className="w-5 h-5 text-white" />,
];

const COLORS = [
  "bg-emerald-100 border-emerald-300",
  "bg-emerald-100 border-emerald-300",
  "bg-purple-100 border-purple-300",
  "bg-blue-50 border-blue-200",
  "bg-amber-50 border-amber-200",
  "bg-gray-100 border-gray-300",
];

export default function MapPage() {
  const { user } = useUserStore(); // MODIFICATION: Add state for lessons, loading, and errors

  const [lessons, setLessons] = useState<Lesson[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null); // MODIFICATION: Add useEffect to fetch data on component mount

  useEffect(() => {
    const fetchSyllabuses = async () => {
      try {
        setIsLoading(true);
        const response = await fetch(
          `${process.env.NEXT_PUBLIC_SERVER_URL}/api/syllabuses`
        ); // Calls your backend
        if (!response.ok) {
          throw new Error("Failed to fetch data from server");
        } // The backend returns an object like { syllabuses: [...] }

        const data: { syllabuses: SyllabusInfo[] } = await response.json(); // *** IMPORTANT: Data Transformation *** // Your backend data (SyllabusInfo) must be mapped to your frontend (Lesson)

        const mappedLessons: Lesson[] = data.syllabuses.map(
          (syllabus, index) => {
            // This is placeholder logic.
            // Ideally, the user's 'status' (completed, etc.)
            // should come from a different API call.
            let status: LessonStatus = "upcoming";
            if (index === 0) status = "in-progress"; // Example: make the first one "in-progress"

            return {
              id: syllabus.id.toString(),
              title: syllabus.title,
              description: syllabus.description,
              // Missing data (we use placeholders or derived values)
              status: status, // Placeholder status
              duration: `${syllabus.lesson_count * 5} min`, // Guessing 5 min per lesson
              participants: 0, // Placeholder
              // Use static arrays for presentation
              color: COLORS[index % COLORS.length], // Cycle through colors
              icon: ICONS[index % ICONS.length], // Cycle through icons

              // Use the real ID for the link
              link: `/study/learn/topic/${syllabus.id}`,
            };
          }
        );

        setLessons(mappedLessons);
        setError(null);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchSyllabuses();
  }, []); // The empty array [] means this runs only once when the component mounts // MODIFICATION: These calculations now depend on the fetched state

  const completedCount = lessons.filter((l) => l.status === "completed").length;
  const totalCount = lessons.length;
  const progressPercentage =
    totalCount > 0 ? (completedCount / totalCount) * 100 : 0;

  return (
    <div className="flex flex-col gap-y-16 xl:flex-row w-[90%] justify-between mx-auto mt-20">
      {/* LEFT SECTION (60%) - Learning Path */}{" "}
      <div className="xl:w-[65%] max-w-5xl mx-auto">
        <LearningSummary /> {/* MODIFICATION: Add loading and error handling */}{" "}
        {isLoading && (
          <div className="text-center text-lg mt-10">
            Loading learning path...
          </div>
        )}{" "}
        {error && (
          <div className="text-center text-lg mt-10 text-red-500">
            Error: {error}{" "}
          </div>
        )}{" "}
        {/* MODIFICATION: Only show timeline if loading is done and no error */}{" "}
        {!isLoading && !error && lessons.length > 0 && (
          <div className="relative mx-auto">
            {/* Timeline Line */}{" "}
            <div className="absolute left-1/2 top-6 bottom-0 w-1 -ml-0.5 bg-gradient-to-b from-[#F66868]/20 to-[#F66868]/80" />{" "}
            <div className="space-y-8">
              {" "}
              {/* This mapping logic works perfectly with the new 'lessons' state */}{" "}
              {lessons.map((lesson, index) => {
                const isLeft = index % 2 === 0;

                return (
                  <div key={lesson.id} className="relative">
                    {" "}
                    {/* ... (rest of your node and card mapping logic) ... */}
                    {/* Node on line */}{" "}
                    <div
                      className={`absolute left-1/2 top-6 -ml-4 w-8 h-8 rounded-full border-4 border-white flex items-center justify-center z-10 bg-[#F66868]`}
                    >
                      {" "}
                      {lesson.status === "completed" && (
                        <CheckCircle2 className="w-4 h-4 text-white" />
                      )}
                      _{" "}
                      {lesson.status === "in-progress" && (
                        <Play className="w-4 h-4 text-white" />
                      )}{" "}
                      {lesson.status === "upcoming" && (
                        <SquareDashed className="w-4 h-4 text-white" />
                      )}
                      _{" "}
                      {lesson.status === "locked" && (
                        <Lock className="w-3 h-3 text-white" />
                      )}{" "}
                    </div>
                    {/* Alternating Cards */}{" "}
                    <div className="grid grid-cols-2 gap-8 items-center justify-center">
                      {" "}
                      {isLeft ? (
                        <>
                          {/* Left Card */}{" "}
                          <div className="pr-8">
                            {" "}
                            <MedicalTerminologyCard lesson={lesson} />{" "}
                          </div>
                          {/* Right Image */}{" "}
                          <div className="relative flex w-fit mx-auto justify-center">
                            {" "}
                            <div className="absolute inset-0 bg-green-500 blur-2xl opacity-20 group-hover:opacity-30 transition-opacity duration-300" />
                            _{" "}
                            <Image
                              src="/hulk.svg"
                              alt="lesson_decoration"
                              width={140}
                              height={140}
                              className="relative z-10 mx-auto animate-float"
                            />{" "}
                          </div>{" "}
                        </>
                      ) : (
                        <>
                          {/* Left Image */}{" "}
                          <div className="relative flex w-fit mx-auto justify-center">
                            {" "}
                            <div className="absolute inset-0 bg-[#F66868] blur-2xl opacity-20 group-hover:opacity-30 transition-opacity duration-300" />{" "}
                            <Image
                              src="/bat.svg"
                              alt="lesson_decoration"
                              width={140}
                              height={140}
                              className="relative z-10 mx-auto animate-float"
                            />{" "}
                          </div>
                          {/* Right Card */}{" "}
                          <div className="pl-8">
                            {" "}
                            <MedicalTerminologyCard lesson={lesson} />{" "}
                          </div>{" "}
                        </>
                      )}{" "}
                    </div>{" "}
                  </div>
                );
              })}{" "}
            </div>{" "}
          </div>
        )}{" "}
      </div>
      {/* RIGHT SECTION (30%) - This part remains the same */}
      NM
      <div className="xl:w-[33%] bg-[#FFF5F5] border border-[#F66868]/20 p-4 h-fit rounded-2xl shadow-sm relative">
        {/* ... (all your existing JSX for the right sidebar) ... */}{" "}
      </div>{" "}
    </div>
  );
}
